body, html {
  margin: 0;
  padding: 0;
  font-family: sans-serif;


  background: url(/images/bg.jpg) no-repeat;
  background-color: #002346;
  background-position: center -20px;
  // background-size: containt
  max-width: 992px;
  min-width: 576px;
  min-height: 100vh;
  border: 1px solid;
  margin: 0 auto; }

.App {

  position: relative;
  max-width: inherit;
  min-width: inherit;
  // border: 1px solid pink
  margin: 0 auto;


 }  // css transition group

.fade-appear {
  z-index: 1;
  opacity: 0;
  transform: translate(0, 100px); }

.fade-appear.fade-appear-active {
  opacity: 1;
  // transition: all 2s ease
  transform: translate(0, 0px);
  transition: all 1s cubic-bezier(0,1.11,.06,.99); }

.fadeRegister-appear {
  z-index: 1;
  opacity: 0; }
.fadeRegister-appear.fadeRegister-appear-active {
  opacity: 1;
  transition: all 1s linear; }

